.top {
  height: 10vh;
  /*This part is important for centering*/
  display: flex;
  align-items: left;
  justify-content: left;
}

.typing-demo {
  width: 34ch;
  animation: typing 2s steps(22), blink .5s step-end ;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px ;
  font-size: 2em;
  font-weight: 700;
  color:#84CEEB;
}



@keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}